@use '../../../scss/' as *;
.navBar {   
    @include mobile {
        display: none;
    }
    a+a {
        margin-left: 48px;
    }
    a:hover {
        text-decoration: underline;
        text-decoration-color: red ;
        transition: 150ms ease;
    }
}
.icon {
    height: 16px;
    width: 24px;
}

